<template>
 <div class="test">
    <answer-mobile :id="id" v-if="isMobile"></answer-mobile>
    <answer-pc :id="id" v-if="!isMobile" type="out"></answer-pc>
 </div>
</template>

<script>
import AnswerPc from '../components/AnswerPc'
import AnswerMobile from '../components/AnswerMobile'
export default {
 name: 'Test',
 components: {AnswerPc, AnswerMobile},
 data() {
 return {
   id:this.$route.query.id || '',
   isMobile:false
 }
 },
 created(){
   this.getOSType()
 },
 mounted(){},
 methods:{
   getOSType(){
     if(/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
        console.log('当前环境-移动端')
        this.isMobile=true
        } else {
            console.log('当前环境-PC端')
            this.isMobile=false
      }
   },
   //判断当前环境是否是微信环境
   is_weixin(){      
      var ua = navigator.userAgent.toLowerCase();
      if(ua.match(/MicroMessenger/i)=="micromessenger") {
           return true;
     } else {
            return false;
      }
  }
 }
}
</script>

<style lang='scss' scoped>
 .test{
     width: 100%;
    height: 100%;
 }
</style>



