<template>
 <div class="answer-mobile">
    <div class="answer-mobile_bg"></div>
    <div class="answer-mobile_wrap">
        <div class="mobile_head">
            <p>{{formData.formName}}</p>
            <div class="mobile_head_desc">{{formData.remarks}}</div>
        </div>
        <div class="mobile_content">
           <div  v-for="(vo, index) in list" :key="index" class="m_question">
               <div v-if="vo.type === 'baseRemark'" class="m_question_title">
                 <span>{{vo.title}}</span>
               </div>
               <div v-if="vo.type === 'splitLine'" class="m_question_title">
                  <div class="line"></div>
               </div>
               <div v-if="vo.type !== 'splitLine' && vo.type !== 'baseRemark'" class="m_question_title" :class="{'m-required': vo.required}">
                  <span class="qnum">{{vo.sort}}.</span><span>{{vo.title}}</span>
               </div>
               <div v-if="vo.type ==='gradeRadio' || vo.type ==='sex'" class="m_question_body">
                  <ul class="radio-multi-ul">
                     <li v-for="(single, n) in vo.items" :key="n" @click="radioSelect(index, n)" class="radio_wrap" :class="{'radio_checked': single.checked}"><span></span><label>{{single.text}}</label></li>
                  </ul>
               </div>
               <div v-if="vo.type ==='gradeCheckbox'" class="m_question_body">
                  <ul class="radio-multi-ul">
                     <li v-for="(single, n) in vo.items" :key="n" @click="checkboxSelect(index, n, single.checked)"  :class="{'is_checked': single.checked}"><span></span><label>{{single.text}}</label></li>
                  </ul>
               </div>
               <div v-if="vo.type === 'baseGauge'" class="m_question_body">
                   <div class="mark-box">
                       <mobile-rate :total="vo.itemNum" :parentIndex="index" @change="myRateChange"></mobile-rate>
                       <p class="tip"><label>{{vo.startDesc}}</label><label>{{vo.endDesc}}</label></p>
                   </div>
               </div>
               <div  v-if="vo.type === 'matrixGauge'" class="m_question_body">
                   <div  v-for="(row, rowIndex) in vo.rowDesc" :key="rowIndex" class="mark-box">
                       <p class="mark_title">{{row.text}}</p>
                       <mobile-rate :total="vo.itemNum" :parentIndex="index" :childIndex="rowIndex" @change="myRateChange"></mobile-rate>
                       <p class="tip"><label>{{vo.startDesc}}</label><label>{{vo.endDesc}}</label></p>
                   </div>
               </div>
               <div v-if="vo.type ==='baseText' || vo.type === 'baseTextArea' || vo.type ==='name' || vo.type ==='phone' || vo.type ==='email' || vo.type ==='academy' || vo.type ==='specialty' || vo.type ==='industry' || vo.type ==='profession'" class="m_question_body">
                  <mobile-input :type="vo.type === 'baseTextArea' ? 'textarea' : 'text'" v-model="vo.questionValue"></mobile-input>
               </div>
               <div  v-if="vo.type === 'uploadFile'" class="m_question_body">
                  <div @click="chooseFile(index)" class="m_upload">
                     <input type="file" name="file"  ref="inputFile" @change="uploadChange" style="display:none">
                     <div class="m_upload_btn"><label>选择文件（30M以内）</label></div>
                  </div>
                  <div v-if="vo.questionValue" class="m_upload_success">
                     <label>{{vo.desc}}</label>
                     <span @click="uploadDel(index)"></span>
                  </div>
               </div>
           </div>
            <div class="end_remarks">{{formData.endWord}}</div>
        </div>
        <div class="mobile_footer">
           <span @click="submit" class="m_button">提交</span>
        </div>
    </div>
 </div>
</template>

<script>
//设置head的meta标签
document.getElementsByTagName('meta')['viewport'].setAttribute('content', 'initial-scale=1, maximum-scale=1, minimum-scale=1, user-scalable=no, viewport-fit=cover');

 import MobileRate from '@/components/MobileRate/index'
 import MobileInput from '@/components/MobileInput/index'

export default {
 name: 'AnswerMobile',
 components: {MobileRate, MobileInput},
 props: {
    id: {
      type: String,
      default: ''
    },
  },
 data() {
 return {
   formData:{
     formName:'',
     remarks:'',
     endWord:''
   },
   list:[],
   isSubmit:false,
   inputFileIndex:null,
   form:{
     formId:'',
     createUser:'',
     data:'',
     ip:''
   },
   api:{
      uploadUrl:`${this.API_URL}/system/file/editorUpload?x-token=${localStorage.getItem("x_token")}`,
      queryQuestionAll:'/form/rest/FormQuestionBaseBusiness/queryQuestionAll/v1',
      save:'/form/rest/FormAnserBusiness/save/v1'
   }
 }
 },
 created(){
    console.log('你来')
    this.initData()
 },
 mounted(){},
 methods:{
    initData(){
      const postData={
         formId:this.id
      }
      this.$post(this.api.queryQuestionAll , postData)
         .then(res => {
            if(res.code ===200){
                  let num=0
                  this.formData=res.data.form
                  const arr=res.data.questions || []
                  arr.forEach((_vo, _index) =>{
                     if(_vo.type ==='gradeRadio' || _vo.type ==='sex' || _vo.type === 'gradeCheckbox'){
                           _vo.items.forEach(item =>{
                              item.checked =false
                           })
                     }
                     if(_vo.type === 'gradeCheckbox' || _vo.type === 'matrixGauge'){
                           _vo.questionValue=[]
                     }else{
                        _vo.questionValue=''
                     } 
                     if(_vo.type === 'splitLine' || _vo.type === 'baseRemark'){
                        _vo.sort=null
                     }else{ 
                        num+=1
                        _vo.sort=num
                     }
                  })
                  this.list=arr
           }
         })
    },
    radioSelect(index, n){
      this.list[index].items.forEach(vo =>{
         vo.checked=false
      })
      this.$nextTick(()=>{
         this.list[index].items[n].checked=true
      })
     
    },
    checkboxSelect(index, n, value){
     this.$nextTick(()=>{
        if(value){
            this.list[index].items[n].checked=false
        }else{
           this.list[index].items[n].checked=true
        }
         
      })
    },
    myRateChange(value, parentIndex, childIndex){
      if(childIndex === null){ 
         this.list[parentIndex].questionValue=value   
      }else{ 
         this.list[parentIndex].rowDesc[childIndex].questionValue=value
      } 
    },
    chooseFile(index){
       this.inputFileIndex=index
       this.$refs.inputFile[0].click()    
    },
    uploadChange(){
      let files=this.$refs.inputFile[0].files[0]
      const isLt2M = files.size > 31457280;
      if(isLt2M){
          this.$notify.error({
                title: 'error',
                duration:1500,
                message: '请选择30M以内文件'
            })
          return
      }
      let formData = new window.FormData()
      formData.append('file', files)
      this.$post(this.api.uploadUrl,formData).then(res =>{
         if(res.success ===0){
              this.list[this.inputFileIndex].desc=files.name
              this.list[this.inputFileIndex].questionValue=res.id
          }else{
              this.$notify.error({
                title: 'error',
                duration:1500,
                message: '上传失败，请重新上传'
            })
          }
      })
    },
    uploadDel(index){
      this.list[index].questionValue=''
    },
    submit(){
      if(this.isSubmit){
          this.$message({
            message: '请勿重复提交！',
            type: 'warning'
          });
            
         return
      }
      //必填项校验
      if(!this.validateRequired(this.list)){
          return
      } 
      const newList=JSON.parse(JSON.stringify(this.list))
      const submitArr=[]
      newList.forEach(item =>{
            if(item.type !== 'baseRemark' && item.type !== 'splitLine' ){    
                let qVal=null
                if(item.type ==='gradeRadio' || item.type ==='sex' || item.type === 'gradeCheckbox'){
                     const checkedArr=[]
                     item.items.forEach(vo => { if(vo.checked){checkedArr.push(vo.text)}})
                      qVal= item.type === 'gradeCheckbox' ? checkedArr : checkedArr.join(',')
                 }else if(item.type === 'gradeCheckbox'){
                    qVal=item.questionValue 
                }else if(item.type === 'matrixGauge'){
                    const matrixGaugeArr=[]
                    item.rowDesc.forEach(vo => { if(vo.questionValue){matrixGaugeArr.push(vo.questionValue)}})
                    qVal=matrixGaugeArr
                }else{
                    qVal=item.questionValue  
                }
                submitArr.push({
                    questionId:item.id,
                    questionValue: qVal
                })
                
        }         
             
       })

       this.form.formId=this.id;
       this.form.data=JSON.stringify(submitArr)
       this.form.ip=localStorage.getItem('Ip')
       this.saveFn()
    },
    saveFn(){
       this.isSubmit=true;
       this.$post(this.api.save, this.form).then(res =>{
           if(res.code ===200){      
               this.$notify.success({
                    title: 'success',
                    duration:1500,
                    message: '提交成功'
                    
                })
           }else{
               this.isSubmit=false;
               this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.msg
                })
           }
       }).catch(err =>{
           this.isSubmit=false
       }) 
    },
    //校验函数
     validateRequired(arr){
        let validate =false;
        for(let i=0; i<arr.length; i++){
            if(arr[i].type !== 'baseRemark' && arr[i].type !== 'splitLine' && arr[i].required){  
                     const msg= (scope) => `${arr[i].sort}. ${arr[i].title} ${scope}`
                    if(arr[i].type ==='gradeRadio' || arr[i].type === 'sex' || arr[i].type ==='gradeCheckbox'){
                        const itemArr= arr[i].items
                        let numb=0;
                        for(let j=0; j<itemArr.length; j++){
                             if(itemArr[j].checked){
                                 numb+=1
                             }
                        }
                        if(numb ===0){
                              this.$message.error(msg('是必选项，请选择选项'));
                              validate=false;
                              break;
                        }else{
                          validate=true;  
                        }    
                    }else  if(arr[i].type ==='baseGauge' && arr[i].questionValue === ''){
                        this.$message.error(msg('是必选项，请为选项打分'));
                        validate=false;
                        break;
                    }else  if(arr[i].type ==='matrixGauge'){
                        let num=0;
                        arr[i].rowDesc.forEach(vo =>{
                             if(vo.questionValue) num ++
                        })
                        if(num!==arr[i].rowDesc.length){
                            this.$message.error(msg('是必选项，请为所有选项打分'));
                            validate=false;
                             break;
                        }else{
                            validate=true;  
                        }    
                    }else if(arr[i].type ==='phone'){
                        if(arr[i].questionValue){
                             const reg = /^[1][3,4,5,7,8][0-9]{9}$/;
                             if (reg.test(arr[i].questionValue)){
                                  validate=true; 
                             }else{  
                                this.$message.error(msg('请输入正确的手机号码'));
                                validate=false;
                                break; 
                             }
                        }else{
                            this.$message.error(msg('是必填项，请输入内容'));
                            validate=false;
                            break; 
                        }

                    }else if(arr[i].type ==='email'){
                        if(arr[i].questionValue){
                             const reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
                             if (reg.test(arr[i].questionValue)){                
                                    validate=true; 
                             }else{
                                  this.$message.error(msg('请输入正确的邮箱地址'));
                                   validate=false;
                                   break; 
                             }
                        }else{
                            this.$message.error(msg('是必填项，请输入内容'));
                            validate=false;
                            break; 
                        }

                    }else if(!arr[i].questionValue){
                       this.$message.error(msg('是必填项，请输入内容'));
                       validate=false;
                         break;
                    }else{
                        validate=true;
                    }
                                        
            }
        }
        
        return validate
    }
 }
}
</script>

<style lang='scss' scoped>
 @function vw($px) {
      @return ($px / 750) * 100vw;
 }
.answer-mobile{
    width:100%;
    height:100%;
    box-sizing: border-box;
    text-align:left;
    font-size:vw(28);
    color: #333;
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    font-family: "-apple-system,BlinkMacSystemFont,Helvetica Neue,PingFang SC,Microsoft YaHei,Source Han Sans SC,Noto Sans CJK SC,sans-serif";
  .answer-mobile_bg{
    position: fixed;
    top: 0px;
    width: 100%;
    bottom: 0;
    z-index: -1;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom center;
  }
  .answer-mobile_wrap{
    position: relative;
    min-height: 100%;
    background: rgba(255,255,255,1);
    .mobile_head{
         margin: 0;
         padding:vw(60) vw(36) vw(24);
         background-color: transparent;
         box-shadow: none;
       p{
          font-size: vw(40);
          color: #666;
       }
       .mobile_head_desc{
          font-size:vw(28);
          color:#333;
          margin: vw(40) 0 0;
          word-wrap: break-word;
       }
    }
    .mobile_content{
       .m_question{
            margin: 0 auto;
            padding:vw(20) vw(36);
            .m_question_title{
              font-size:vw(36); 
              color:#333;
              margin-bottom:vw(24);  
               span:last-child{
                  word-break: break-all;
               }
              .line{
                  height: 0;
                  width: 100%;
                  border-top: dashed 1px #aaa;
              }          
            }
            .m-required>*:last-child:after{
                     content: "*";
                     display: inline;
                     color: #ff6d56;
                     margin-left: vw(10);
                     font-size:vw(24);
                     vertical-align: center;      
                    }
            .m_question_body{
               .radio-multi-ul{
                  li{
                     width:100%;
                     height:vw(80);
                     display:flex;
                     align-items:center;
                     span{
                        display:inline-block;
                        width:vw(30);
                        height:vw(30);
                        border: vw(2) solid #dde5ed;
                        background: #fff;     
                        margin-right:vw(20);     
                        position:relative;          
                     }
                     label{
                        font-size:vw(32);
                        color:#666;
                        word-wrap: break-word;
                        overflow: hidden;
                        width:vw(600);
                     }
                  }
                  .is_checked{
                     span{
                        border-color:#03B187;
                     }
                     span:after{
                        content:'\2713';
                        font-size:vw(32);
                        font-weight:600;
                        color:#03B187;
                        position:absolute;
                        left: vw(1);
                        bottom: - vw(4);
                     }
                     label{
                        color:#03B187;
                     }
                  }
                  .radio_wrap{
                     span{
                       border-radius:100%;
                     }             
                  }
                  .radio_checked{
                     span{
                        border-color:#03B187;
                     }
                     span:after{
                        content:'';
                        width:vw(22);
                        height:vw(22);
                        border-radius:100%;
                        background:#03B187;
                        position:absolute;
                        left: 0;
                        top:0;
                        bottom:0;
                        right:0;
                        margin:auto;
                     }
                     label{
                        color:#03B187;
                     }
                  }
                  
               }
               .mark-box{
                  .mark_title{
                     line-height:vw(80);
                     font-size:vw(32);
                     color:#666;
                  }
                 .tip{
                    height:vw(80);
                    display:flex;
                    justify-content: space-between;
                    align-items: center;
                    label{
                       font-size:vw(24);
                       color:#999;
                    }       
                 }
               }
               .m_upload{
                  padding: vw(18) vw(4) vw(18) vw(36);
                  border: 1px solid #DDE5ED;
                  border-radius: vw(4);
                  font-size: vw(24);
                  color: #666;
                  position:relative;
                  display:flex;
                  justify-content: center;
                  .m_upload_btn{                
                     label:before{
                      content:'\21E7';
                      font-weight:900;
                      color:#03B187;
                      margin-right: vw(16);
                     }
                  }
               }
               .m_upload_success{
                  padding: vw(18) vw(4) vw(18) vw(36);
                  border: 1px solid #DDE5ED;
                  border-radius: vw(4);
                  font-size: vw(24);
                  color: #666;
                  margin-bottom: vw(2);
                  position: relative;
                  display:flex;
                  align-items: center;
                  img{
                     width:vw(40);
                     height:vw(40);
                     margin-right:vw(20);
                  }
                  span{
                     display:inline-block;
                     width:vw(30);
                     height:vw(30);
                     border-radius:100%;
                     background:#ff6d56;
                     position: absolute;
                     top:- vw(15);
                     right:- vw(15);
                     display: flex;
                     justify-content: center;
                     align-items: center;
                  }
                  span:after{
                     content:'';
                     display: inline-block;
                     width:vw(14);
                     height:vw(5);
                     background:#fff;
                  }
               }
            }
       }
       .end_remarks{
         padding:vw(20) vw(36);
         color:#999;
        font-size:vw(28);
       }
    }
    .mobile_footer{
       padding:vw(20) vw(36) vw(100) vw(36);
       .m_button{
         display: inline-block;
         zoom: 1;
         vertical-align: baseline;
         cursor: pointer;
         text-align: center;
         text-decoration: none;
         width: 100%;
         background-color: #03B187;
         color: #fff;
         height:vw(80);
         line-height: vw(80);
         font-size:vw(36);
         border-radius: vw(8);
         cursor: pointer;
         box-shadow: 0 vw(4) vw(8) rgba(0, 0, 0, .07);
       }
    }
  }
}
 ::-webkit-scrollbar {
      width: 1px;
      height: 1px;
   }
   ::-webkit-scrollbar-track-piece {
      background-color: #ebeef3;
      -webkit-border-radius: 2px;
   }
   ::-webkit-scrollbar-thumb:vertical {
      height: 32px;
      background-color: #d8dce4;
      -webkit-border-radius: 2px;
   }
   ::-webkit-scrollbar-thumb:horizontal {
      width: 32px;
      background-color: #d8dce4;
      -webkit-border-radius: 2px;
   }
</style>



