<template>
 <div  class="mobile-rate">
    <div  @click="rateSelect(index, vo.num)" v-for="(vo, index) in rateList" :key="`rate_${index}`" class="mobile-rate_item" :class="{'active': vo.isActive, 'ten_num': total === 10}">
        {{vo.num}}
    </div>
 </div>
</template>

<script>
export default {
 name: 'MobileRate',
 props:{
     total:{
         type:Number,
         default:5
     },
      start:{
         type:Number,
         default:null
     },
     parentIndex:{
         type:Number,
         default:null
     },
     childIndex:{
         type:Number,
         default:null
     }
 },
 data() {
 return {
   rateList:[]
 }
 },
 created(){
     for(let i=1; i<=this.total; i++){
         this.rateList.push({
             num:i,
             isActive: this.start && i <= this.start ? true : false 
         })
     }
 },
 mounted(){},
 methods:{
     rateSelect(index, num){
         this.isShowFn(index)
         this.$emit('change', num, this.parentIndex, this.childIndex)
     },
     isShowFn(index){
        this.rateList.forEach((item, i) =>{
            if(i <=index){
                item.isActive=true
            }else{
                item.isActive=false
            }
        })
     }
 }
}
</script>

<style lang='scss' scoped>
 @function vw($px) {
      @return ($px / 750) * 100vw;
 }
.mobile-rate{
    display:flex;
    justify-content: space-between;
    align-items: center;
    .mobile-rate_item{
       width:vw(120);
       height:vw(60);
       border-radius:vw(4);
       background: #EAEAEA;
       color:#666;
       display:flex;
       align-items: center;
       justify-content: center;
    }
    .ten_num{
        width:vw(60);
    }
    .active{
        background:#03B187;
        color:#fff;
    }
}
</style>



