<template>
 <div class="mobile-input "  :class="{'mobile-input--active': isFocus}">
    <input 
     v-if="type === 'text'"
     type="text"
     v-model="innerText"
     @focus="isFocus=true"
     @blur="isFocus=false"
     @input="changeText"/>
     <textarea  
        v-if="type === 'textarea'"
        type="text" 
        cols="20" 
        rows="1"
        v-model="innerText"
        @focus="isFocus=true"
        @blur="isFocus=false"
        @input="changeText">
     </textarea>
 </div>
</template>

<script>
export default {
 name: 'MobileInput',
 props:{
      value:{
         type:String,
         default:''
     },
     type:{
         type:String,
         default:'text'
     }
 },
 data() {
 return {
   innerText:this.value,
   isFocus:false
 }
 },
 created(){},
 mounted(){},
 methods:{
     changeText(e){
        this.$emit('input', this.innerText)
     }
 }
}
</script>

<style lang='scss' scoped>
 @function vw($px) {
      @return ($px / 750) * 100vw;
 }
.mobile-input{
    height:vw(70);
    border: solid 1px #dde5ed;
    border-radius:vw(4);
    input, textarea{
        display: block;
        width: 100%;
        height:100%;
        box-sizing: border-box;
        border: 0;
        background-color: transparent;
        box-shadow: none;                 
        outline: none;
        -webkit-appearance: none;
        font-size:vw(32);
        color:#333;
        padding:vw(10) vw(20);
    }
}
.mobile-input--active{
    border-color:#03B187;
    box-shadow:0 vw(4) vw(8) 0 rgba(38, 114, 255, .2);
}
</style>



